@include tablet {
  .contenedor-anuncios {
    @include grid(3, 3rem);
  }
}
.anuncios {
  background-color: $gris;
  border: 3px solid $negro;
  
  margin-bottom: 1rem;
  overflow: hidden;
    
  .contenido-anuncio {
    
    padding: 2rem;
    
   
  }
  h3, p {
    margin: 0;
    overflow: hidden;
  }
 
  
}
.precio {
  color: $verde;
  font-weight: $bold;
  font-size: 3rem;
}
.iconos-caracteristicas{
    display: flex;
    padding: 0;
    list-style: none;
    align-items: center;
    margin: 1rem auto;
    justify-content: space-evenly;
    max-width: 40rem;
    
    li{
        display: flex;
        justify-content: space-between;
        gap: 1rem;
      
        img{
            flex: 0 0 3rem;
           
          
                                   
        }
        p{
            font-weight: $bold;
         
            
        }
       
    }
   
  
}
