.formulario{
    p{
        font-size: 1.4rem;
        color: $grisOscuro;
        margin: 2rem 0 0 0;
    }
    legend{
        font-size: 2rem;
        color: $grisOscuro
    }
    label{
        font-weight: $bold;
        text-transform: uppercase;
        display: block;
    }
    input:not([type="submit"]), textarea, select{
        padding: 1rem;
        display: block;
        border: 1px solid $grisOscuro;
        border-radius: 1rem;
        margin-bottom: 2rem;
        width: 100%;

    }
    textarea{
        height: 10rem;
    }
    .forma-contacto{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 30rem;
    }
    input[type="radio"]{
        margin: 0;
        width: auto;

    }
}