.dark{
    background-color: $negro;
    p{
        color: $blanco;
    }
    h1,h2,h3,h4{
        color: $gris;
    }
    .anuncios, .resumen-propiedad{
        background-color: $grisOscuro;
        p, h1,h2,h3,h4{
            color: $gris;
        }
        .precio{
            color: $verde;
        }
        .iconos{
            filter:invert(100%);
        }
       
    }
    .texto-nosotros blockquote {
        color: $blanco;
    }
    .formulario{
        label, legend,p{
            color: $blanco;
        }
        input:not([type="submit"]), textarea, select{
            background-color: $gris;
        }
    }
    td{
        color: $blanco;
    }
}