.alerta {
  text-align: center;

  color: $blanco;
  padding: 0.5rem;
  margin-top: 1rem;
  font-weight: $bold;
  text-transform: uppercase;
  &.error {
    background-color: $rojo;
  }
  &.exito{
    background-color: $verde;
  }
}


